:root {
  --primary-color: #00acdc;
}

.container {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.navList {
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.link {
  display: flex;
}

.link:not(:first-child) {
  margin-left: 20px;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav--link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
  position: relative;
}

.is-active, .link:hover a {
  color: var(--primary-color);
}

.is-active:after {
  content: "";
  background-color: var(--primary-color);
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  inset: 56px 0 0;
}
